export default {
  props: {
    options: {
      type: Array,
      require: true
    },
    search: {
      type: Boolean,
      default: false
    },
    searchResult: {
      type: String,
      default: "Ничего не найдено"
    },
    selectValue: {
      type: String,
      default: ""
    },
    searchValue: {
      type: String,
      default: ""
    },
    showOptions: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    changeSelectValue(option) {
      this.$emit("select", option)
      this.$emit("show", !this.showOptions)
      this.$emit("input", "")
    }
  }
}
